:root {
  --error-message-color: red;
}

/* Login.css */
.login-container {
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 2rem;
  border: 1px solid var(--table-border-color);
  border-radius: 12px;
  box-shadow: 0 10px 20px var(--modal-box-shadow);
  background-color: var(--modal-bg-color);
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  text-align: center;
}

.login-container:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 25px var(--modal-box-shadow);
}

.login-container h2 {
  margin-bottom: 1.5rem;
  color: var(--heading-color);
  font-size: 1.8rem;
  font-weight: bold;
}

.login-container form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-container label {
  margin-bottom: 0.5rem;
  color: var(--text-color);
  font-size: 1rem;
}

.login-container input {
  width: 100%;
  margin-bottom: 1rem;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
  background-color: var(--background-color);
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}

.login-container input:focus {
  background-color: var(--modal-bg-color);
  border-color: var(--heading-color);
  outline: none;
}

.login-container button {
  width: 100%;
  padding: 0.75rem;
  border: none;
  border-radius: 8px;
  background-color: var(--button-bg-color);
  color: var(--button-text-color);
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.login-container button:hover {
  background-color: var(--button-hover-bg-color-alt);
  box-shadow: 0 5px 10px var(--modal-box-shadow);
}

.login-container p {
  margin-top: 1rem;
  color: var(--text-color);
}

.login-container p a {
  color: var(--button-bg-color);
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease-in-out;
}

.login-container p a:hover {
  color: var(--button-hover-bg-color-alt);
  text-decoration: underline;
}

.error-message {
  color: var(--error-message-color);
  text-align: center;
  margin-bottom: 1rem;
  font-size: 1rem;
  font-weight: bold;
}


.google-login-button {
  background-color: #4285f4;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin: 10px 0;
  width: 100%;
}

.google-login-button:hover {
  background-color: #357abd;
}


/* Exemplo de estilo para o boto de login */
.google-login-button {
  padding: 10px 20px;
  background-color: #4285F4;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.google-login-button:disabled {
  background-color: #dcdcdc;
  cursor: not-allowed;
}

/* Exibir as informaes do usurio */
.user-info {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.user-info img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
